<template>
  <div class="text-white">
    <div class="my-2 text-center">
      <p class="text-gray-900 dark:text-white">
        To know how to operate the whole program, please watch this tutorial.
      </p>
      <a href="https://youtube.com/playlist?list=PLqyVsQxV6gqfpfLl_golBk44xx3TopB09" target="_blank"
        class="inline-block px-3 py-2 mt-3 text-center text-white transition bg-green-500 rounded hover:bg-green-600">
        Tutorial</a>
    </div>
    <div class="container p-5 mx-auto mt-5 space-y-3 text-white bg-gray-800 rounded text-ceter" v-if="!loading">
      <template v-for="(p, i) in userData" :key="i">
        <p class="text-lg" v-if="i && p && i !== 'pass' && i !== 'cpass'">
          <span class="capitalize">{{ i }}</span> : {{ p }}
        </p>
      </template>
    </div>
    <div>
      <div class="text-center" v-if="loading">
        <div class="text-white spinner-border" role="status">

          <span class="">Loading...</span>
        </div>
      </div>
    </div>
    <div class="text-center">
      <button class="inline-block px-3 py-2 mt-3 text-center text-white transition bg-red-500 rounded hover:bg-red-600"
        @click="signOut">
        Logout
      </button>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      userData: {},
      loading: false,
    };
  },
  methods: {
    getUserData() {
      this.loading = true;
      fetch("https://script.google.com/macros/s/AKfycbwOyp-1jYriwP1BWWe-oFLLyoAugj_blT7hK_hbmhLODL8YGaDaENwY1o5OGg-vAJyG/exec")
        .then((res) => res.json())
        .then((data) => {
          this.userData = data.find(s => s['Email Address'] == this.user.email);
          this.loading = false;
          if (!this.userData) {
            this.$router.push("/kyc")
          }

        });

    },
    ...mapActions(["signOut"]),
  },
  created() {
    this.getUserData();
  },
  computed: {
    ...mapState(["user"]),
  },
};
</script>

